<script>
import jsPDF from 'jspdf'
import domtoimage from 'dom-to-image';
import mapboxgl from 'mapbox-gl'
mapboxgl.accessToken = "pk.eyJ1IjoiY2FydG9uZyIsImEiOiJjazJldzVobGkwOWRxM2hzNTB1M3o2cG94In0.w7FyG31FWqXm3vXSh6WtxQ"
import {toGeoJSON} from '@/helpers/mapUtils'
import AgePyramidChart from '@/components/AgePyramidChart'
import DoughnutChart from '@/components/DoughnutChart'
import PolarChart from '@/components/PolarChart'
import Footer from "@/components/Footer"
const devEnv = process.env.NODE_ENV === 'development'
const imgURL = devEnv ? '/' : `./`

export default {
  name:"ExportPDF",
  components:{
    AgePyramidChart,
    DoughnutChart,
    PolarChart,
    Footer
  },
  mounted(){
    this.showLoaders = true
    this.mapHeight = `${this.$refs.infoBox[0].clientHeight}px`
    this.mapWidth = `${this.$refs.pdfDiv1.clientWidth - this.$refs.infoBox[0].clientWidth}px`
    this.initMap()
  },
  data(){
    return {
      imgURL: imgURL,
      mapHeight: "7cm",
      mapWidth: '7cm',
      images:{},
      imagesLoadedCount: 0
    }
  },
  props: ['dataset'],
  computed:{
    showLoaders:{
      get(){
        return this.$store.state.showLoaders
      },
      set(v){
        this.$store.commit('setShowLoaders', v)
      }
    },
    showPDF:{
      get(){
        return this.$store.state.showPDF
      },
      set(v){
        this.$store.commit('setShowPDF', v)
      }
    },
    camp(){
      return toGeoJSON(this.$store.state.camps.filter(x => x.id == this.$store.state.selectedCamp))
    }
  },
  watch:{
    imagesLoadedCount(){
      if (this.imagesLoadedCount === 6) {
        this.ExportPDF()
      }
    }
  },
  methods:{
    initMap(){
      this.map = new mapboxgl.Map({
        container: 'pdfmap',
        style: 'mapbox://styles/mapbox/streets-v11',
        center: [this.camp.features[0].geometry.coordinates[0], this.camp.features[0].geometry.coordinates[1]],
        zoom: 7,
        preserveDrawingBuffer: true
      })

      this.map.on('load', async () => {
        this.map.resize()
        this.map.loadImage(`${this.imgURL}markers/default.png`, (error, image) => {
           this.map.addImage("default", image)
          if (error) throw error;
        });
        this.map.addSource('camp', {
          'type': 'geojson',
          'data': this.camp
        });
        this.map.addLayer({
          'id': 'camp',
          'type': 'symbol',
          'source': 'camp',
          'layout': {
            'icon-image': 'default',
            'icon-size': 0.025
          }
        })
        this.map.once('style.load', this.generateImage())
      })
    },
    async generateImage(){

      const part1 = new Image()
      part1.src = await domtoimage.toPng(this.$refs.pdfDiv1)
      part1.onload = () => {
        this.images.part1 = part1
        this.imagesLoadedCount ++
      }
      const part2 = new Image()
      part2.src = await domtoimage.toPng(this.$refs.pdfDiv2)
      part2.onload = () => {
        this.images.part2 = part2
        this.imagesLoadedCount ++
      }
      const part3 = new Image()
      part3.src = await domtoimage.toPng(this.$refs.pdfDiv3)
      part3.onload = () => {
        this.images.part3 = part3
        this.imagesLoadedCount ++
      }
      const part4 = new Image()
      part4.src = await domtoimage.toPng(this.$refs.pdfDiv4)
      part4.onload = () => {
        this.images.part4 = part4
        this.imagesLoadedCount ++
      }
      const footerImg = new Image()
      footerImg.src = await domtoimage.toPng(this.$refs.pdfDiv5)
      footerImg.onload = () => {
        this.images.footerImg = footerImg
        this.imagesLoadedCount ++
      }
      const mapImg = new Image()
      mapImg.src = await this.map.getCanvas().toDataURL('image/png')
      mapImg.onload = () => {
        const ratio = this.images.part1.width / 210
        mapImg.width = mapImg.width / ratio
        mapImg.height = mapImg.height / ratio
        this.images.mapImg = mapImg
        this.imagesLoadedCount ++
      }
    },
    ExportPDF(){
      const headerLogoRatio = document.getElementById("pdfLogo").width / document.getElementById("pdfLogo").height;
      const ratio = this.images.part1.width / 210
      const footerRatio = 210 / this.images.footerImg.width

      const doc = new jsPDF();
      doc.addImage(this.images.part1, "JPEG", 0, 0, this.images.part1.width/ratio, this.images.part1.height/ratio);
      doc.addImage(`${this.imgURL}img/sector_logo.png`,5,2,7.5*headerLogoRatio, 7.5)
      doc.addImage(this.images.mapImg, "PNG",1,10, this.images.mapImg.width, this.images.mapImg.height)
      doc.addPage();
      doc.addImage(this.images.part2, "JPEG", 0, 0, this.images.part2.width/ratio, this.images.part2.height/ratio);
      doc.addPage();
      doc.addImage(this.images.part3, "JPEG", 0, 0, this.images.part3.width/ratio, this.images.part3.height/ratio);
      doc.addPage();
      doc.addImage(this.images.part4, "JPEG", 0, 0, this.images.part4.width/ratio, this.images.part4.height/ratio);
      doc.addImage(this.images.footerImg, "JPEG", 0, 295-(this.images.footerImg.height*footerRatio), this.images.footerImg.width*footerRatio, this.images.footerImg.height*footerRatio);
      this.showPDF = false
      this.showLoaders = false
      doc.save();
    }
  }
}
</script>

