<template lang="html">
  <div>
    <div class="traffic-lights-infos">
      <v-speed-dial
        top
        left
        direction="left"
        open-on-hover
        transition="slide-y-reverse-transition"
      >
        <template v-slot:activator>
          <v-btn
            color="primary"
            dark
            fab
          >
            <v-icon >
              mdi-traffic-light
            </v-icon>
          </v-btn>
        </template>
        <div class="d-flex align-center traffic-lights-infos-content">
            <span class="square mr-2 ml-5" style="background-color: #DA5A56"></span>
            <span class="caption">Severity</span>
            <span class="square mr-2 ml-5" style="background-color: #FFA90A"></span>
            <span class="caption">Minimal Severity</span>
            <span class="square mr-2 ml-5" style="background-color: #ffeb0a"></span>
            <span class="caption">Services available but not adequate/Optimum</span>
            <span class="square mr-2 ml-5" style="background-color: #62BB62"></span>
            <span class="caption">Services are available</span>

        </div>
      </v-speed-dial>
    </div>


    <div v-for="cat in selectedSubmission" :key="cat.name" class="mb-3 category-container mt-2">
      <div class="d-flex align-center full-width mb-2 ">
        <img :src="cat.icon" alt="" class="category-icon ml-4"><h1 class="ml-3 secondary--text">{{cat.name}}</h1>
        <div class="flex-grow-1"></div>
        <div class="" v-if="cat.name == 'General'">
          <!-- <v-tooltip top color="green">
            <template v-slot:activator="{ on }">
              <v-btn
                elevation="2"
                icon
                color="green"
                v-on='on'
              >
                <v-icon>mdi-file-delimited-outline</v-icon>
              </v-btn>
            </template>
            <span>Export CSV</span>
          </v-tooltip> -->
          <v-tooltip top color="red">
            <template v-slot:activator="{ on }">
              <v-btn
                elevation="2"
                icon
                color="red"
                v-on='on'
                class="mr-4 ml-1"
                @click="showPDF = true"
              >
                <v-icon>mdi-adobe-acrobat</v-icon>
              </v-btn>
            </template>
            <span>Export PDF</span>
          </v-tooltip>

          <!-- Close Button -->
          <v-tooltip right color="primary">
            <template v-slot:activator="{ on }">
              <v-btn class="ma-2" outlined small fab color="primary" v-on='on' @click="selectedCamp = null">
                <v-icon>mdi-close</v-icon>
              </v-btn>
            </template>
            <span>Clear</span>
          </v-tooltip>
        </div>

      </div>
      <AgePyramidChart
        v-if="cat.name == 'Demographic Distribution' && cat.data['age_pyramid#1']"
        :id="cat.data['age_pyramid#1'][0].value+Math.floor(Math.random() * Math.floor(100000))"
        :surveyData="cat.data['age_pyramid#1']"
      >
      </AgePyramidChart>
      <DoughnutChart
        v-if="cat.name == 'Demographic Distribution' && cat.data['doughnut_chart#2']"
        :id="cat.data['doughnut_chart#2'][0].value+Math.floor(Math.random() * Math.floor(100000))"
        :surveyData="cat.data['doughnut_chart#2']"
      >
      </DoughnutChart>
      <div class="d-flex align-center full-width ml-8" v-for="(item,i) in cat.data" :key="`${item.question}+${i}`">
        <span class="square mr-2" :style="{'background-color': item.tl}" v-if="item.tl != null"></span>
        <span class="subtitle-1">{{item.question}}</span>
        <span class="subtitle-2 secondary--text ml-2">{{item.answer}}</span>
      </div>
      <PolarChart
        v-if="cat.name == 'Shelter - NFI' && cat.data['polar_area_chart#3']"
        :id="cat.data['polar_area_chart#3'][0].value+Math.floor(Math.random() * Math.floor(100000))"
        :surveyData="cat.data['polar_area_chart#3']"
      >
      </PolarChart>
    </div>
    <ExportPDF v-if="showPDF" :dataset="selectedSubmission"></ExportPDF>
  </div>
</template>

<script>
import {isRelevant} from '@/helpers/relevant'
import {groupCategories} from '@/constants/categories'
import {getTrafficLight} from '@/constants/traffic_lights'
import AgePyramidChart from '@/components/AgePyramidChart'
import DoughnutChart from '@/components/DoughnutChart'
import PolarChart from '@/components/PolarChart'
import ExportPDF from '@/components/ExportPDF'
const devEnv = process.env.NODE_ENV === 'development'
export default {
  name:'SurveyViewer',
  components:{
    AgePyramidChart,
    DoughnutChart,
    PolarChart,
    ExportPDF
  },
  computed:{
    selectedCamp:{
      get(){
        return this.$store.state.selectedCamp
      },
      set(c){
        this.$store.commit('setSelectedCamp', c)
      }
    },
    submissions(){
      return this.$store.state.submissions
    },
    surveyConfig(){
      return this.$store.state.surveyConfig
    },
    labels(){
      return this.$store.state.labels
    },
    selectedSubmission(){
      let dataToDisplay = {}
      groupCategories.map(c => {
        dataToDisplay[c.csv_name] = {
          data: [],
          name: c.app_name,
          icon: devEnv ? `/icons/${c.icon}` : `./icons/${c.icon}`
        }
      })

      if (this.selectedCamp != null) {
        for (let sub in this.submissions) {
          if (sub == this.selectedCamp) {
            for (let v in this.surveyConfig) {
              if (isRelevant(this.surveyConfig[v],this.submissions[sub]) && this.submissions[sub][v]) {
                if (!this.surveyConfig[v].chart) {
                  dataToDisplay[this.surveyConfig[v].category_name].data.push({
                    field: this.surveyConfig[v].csv_field,
                    question: this.surveyConfig[v].alias,
                    answer: this.surveyConfig[v].multiple == 'yes' ? this.submissions[sub][v].split(' ').map(x => this.getLabel(x)).join(', ') : this.getLabel(this.submissions[sub][v]),
                    tl: this.surveyConfig[v].traffic_light != '' ? getTrafficLight(this.surveyConfig[v].traffic_light, this.submissions[sub][v]) : null
                  })
                } else {
                  if (dataToDisplay[this.surveyConfig[v].category_name].data[this.surveyConfig[v].chart]) {
                    dataToDisplay[this.surveyConfig[v].category_name].data[this.surveyConfig[v].chart].push({
                      label:this.surveyConfig[v].alias,
                      value: this.submissions[sub][v]
                    })
                  } else {
                    dataToDisplay[this.surveyConfig[v].category_name].data[this.surveyConfig[v].chart] = [{
                      label:this.surveyConfig[v].alias,
                      value: this.submissions[sub][v]
                    }]
                  }
                }
              }
            }
          }
        }
      }
      return dataToDisplay
    },
    showPDF:{
      get(){
        return this.$store.state.showPDF
      },
      set(){
        this.$store.commit('setShowPDF', true)
      }
    }
  },
  methods:{
    getLabel(value){
      for (let label of this.labels) {
        if (label.name == value) {
          return label.label
        }
      }
      return value
    }
  }
}
</script>

